<template lang='pug'>
  div
    CellSwipe(:right-width='65', :left-width='65')
      span(slot='left') 
      CellGroup
        Cell(title='', value='')
      span(slot='right') 
    myCellSwipe
</template>

<script>
import { CellSwipe, Cell, CellGroup } from 'vant'
import myCellSwipe from '@/components/CellSwipe.vue'
export default {
  components: { CellSwipe, Cell, CellGroup, myCellSwipe }
}
</script>

<style scoped>

</style>



